.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#splash-screen {
  color: white;
  font-size: 5vw;
  border-color: black;
  border-width: 2px;
  border-bottom-style: solid;
  padding-left: 5%;
  padding-top: 5%;
  padding-bottom: 5%;
  height:70%;
  text-align: center;
  vertical-align: center;
}

#root{
  height:100%;
}

html,
body{
  background-color: #3c78d8;
  height:100%;
}